import { createChallengeFromFileReadResult } from "@fat-dash/domain/csv-etl/createChallenge"
import { delay } from "@fat-dash/common/async"
import {
  FileReadResult,
  CreateChallengeResult
} from "@fat-dash/domain/csv-etl/types"

function saveChallengeFilesScheduled<F>(
  readFile: (f: F) => Promise<FileReadResult>,
  onChallengeResult: (res: CreateChallengeResult) => void,
  maxConcurrentCount: number
) {
  return async (files: F[]) => {
    let pendingCount = 0
    for (let idx = 0; idx < files.length; idx++)
      while (true)
        if (pendingCount <= maxConcurrentCount) {
          readFile(files[idx]).then(res => {
            pendingCount = Math.max(0, pendingCount - 1)
            onChallengeResult(createChallengeFromFileReadResult(res))
          })
          pendingCount += 1
          break
        } else await delay(0)
  }
}

export { saveChallengeFilesScheduled }
