export type GameVersionToFileCellCoords = {
  [key in GameVersion]: [
    number, // rowindex
    number // columnIndex
  ]
}

// order desc
export const knownGameVersions = ["2.0.1.0", "1.0.6", "0.0.0"] as const

export const gameVersionCells: GameVersionToFileCellCoords = {
  "2.0.1.0": [14, 1],
  "1.0.6": [14, 1],
  "0.0.0": [13, 1]
}

export type GameVersion = typeof knownGameVersions[number]
