import {
  killHeaderNameProperty,
  weaponHeaderNameProperty,
  summaryHeaderNameProperty,
  technicalHeaderNameProperty,
  mapPropertyNames,
  mapHeaderNames
} from "@fat-dash/domain/csv-etl/version/csv-file-template"
import { GameVersion } from "@fat-dash/domain/csv-etl/version/constants"
import { parseKovaakTimeLt106 } from "./0.0.0/parseKovaakTimeLt106"
import { parseKovaakTime2010 } from "./2.0.1.0/parseKovaakTime2010"
import { summaryHeaderNamePropertyV106 } from "./1.0.6/csv-file-template106"

export {
  getMergedChallengePropertyDescriptions,
  getMergedKillPropertyDescriptions,
  getMostRecentKnownGameVersion
} from "./version-helpers"

export function killPropertyNames(_range: GameVersion) {
  return mapPropertyNames(killHeaderNameProperty)
}

export function killHeaderNames(_range: GameVersion) {
  return mapHeaderNames(killHeaderNameProperty)
}

export function weaponPropertyNames(_range: GameVersion) {
  return mapPropertyNames(weaponHeaderNameProperty)
}

export function weaponHeaderNames(_range: GameVersion) {
  return mapHeaderNames(weaponHeaderNameProperty)
}

export function summaryPropertyNames(range: GameVersion) {
  return summaryTuple(range, mapPropertyNames)
}

export function summaryHeaderNames(range: GameVersion) {
  return summaryTuple(range, mapHeaderNames)
}

export function technicalPropertyNames(_range: GameVersion) {
  return mapPropertyNames(technicalHeaderNameProperty)
}

export function technicalHeaderNames(_range: GameVersion) {
  return mapHeaderNames(technicalHeaderNameProperty)
}

function summaryTuple(
  gv: GameVersion,
  fetcher: (template: string[][]) => string[]
) {
  return gv === "0.0.0"
    ? fetcher(summaryHeaderNameProperty)
    : fetcher(summaryHeaderNamePropertyV106)
}

export function parseKovaakTime(gv: GameVersion) {
  return (kTime: string, errorDescr: string) => {
    switch (gv) {
      case "2.0.1.0":
        return parseKovaakTime2010(kTime, errorDescr)
      default:
        return parseKovaakTimeLt106(kTime, errorDescr)
    }
  }
}
