import debug from "debug"
import { Environment } from "@fat-dash/config/Environment"

export const APP_NS = "app"

export const USER_SUB_NS = "user"

export enum Loglevel {
  trace = "trace",
  debug = "debug",
  info = "info",
  warn = "warn",
  error = "error"
}

/**
 * Log only errors:
 * createDebug.enable(`${APP_NS}:*:${Loglevel.error}`)
 *
 * Log only certain modules:
 * createDebug.enable(`${APP_NS}:app/CommandHandler.ts:*`)
 *
 * Exclude certain modules:
 * createDebug.enable(`*,-${APP_NS}:app/Dispatcher.ts:${Loglevel.trace}`)
 *
 * Log only certain loglevel for specific module:
 * debug.enable(`${APP_NS}:common/trace.ts:${Loglevel.trace}`)
 *
 * Multiple log rules:
 * debug.enable(`${APP_NS}:*:${Loglevel.debug},${APP_NS}:common/trace.ts:${Loglevel.trace}`)
 *
 * Enable trace module logging:
 * debug.enable(`${APP_NS}:common/trace.ts:${Loglevel.trace}`)
 */
export function initLogConfig() {
  const logNamespaces = Environment.getLoggingNamespaces()
  if (logNamespaces === undefined) initDefaultEnvironmentLogConfig()
  else debug.enable(logNamespaces)
}

function initDefaultEnvironmentLogConfig() {
  if (Environment.isDevelopment())
    debug.enable(`
    ${APP_NS}:*,
    -${APP_NS}:*:${Loglevel.trace},
    `)
  else if (Environment.isProduction())
    debug.enable(`
    ${APP_NS}:*:${Loglevel.error},
    -${APP_NS}:*:${Loglevel.trace},
    -${APP_NS}:*:${Loglevel.info},
    -${APP_NS}:*:${Loglevel.warn},
    -${APP_NS}:*:${Loglevel.debug},
    `)
  else throw new Error("No suitable Environment found.")
}