function isProduction() {
  return process.env.NODE_ENV === "production"
}

function isDevelopment() {
  return process.env.NODE_ENV === "development"
}

function isBeta() {
  return process.env.IS_BETA === "true"
}

function getDeveloperEmail(): string {
  if (!process.env.DEV_EMAIL) throw new Error("Could not find developer email.")
  return process.env.DEV_EMAIL
}

function getPackageVersion(): string {
  if (!process.env.npm_package_version)
    throw new Error("Could not find npm package version.")
  return process.env.npm_package_version
}

function getLoggingNamespaces(): string | undefined {
  // see 'debug' npm package
  return process.env.DEBUG || undefined
}

const Environment = {
  isProduction,
  isDevelopment,
  isBeta,
  getDeveloperEmail,
  getPackageVersion,
  getLoggingNamespaces
}

export { Environment }
