import { CsvKillStatsRecord } from "./csv-record-types"
import { parseFloatOrThrow } from "@fat-dash/domain/util/number"
import { KillFromFile, ChallengeFileName } from "./types"
import { Header } from "@fat-dash/domain/report/types"
import { stripColon } from "./util"
import {
  getMergedKillPropertyDescriptions,
  parseKovaakTime
} from "./version/version-api"
import { GameVersion } from "./version/constants"

const mapKillFromFile = (
  csvKillSectionRecords: CsvKillStatsRecord[],
  fileName: ChallengeFileName,
  gameVersion: GameVersion
): KillFromFile[] => {
  return csvKillSectionRecords.map(r => {
    // time already given in csvKillStats column in format 'HH:mm:ss[:SSS]',
    // so concatenate this time with date from file info
    const parsedCsvTime = parseKovaakTime(gameVersion)(r.time, fileName.full)

    // challengeDate is local date from csv file name, so do not use utc methods
    const killDate = new Date(fileName.date)
    killDate.setHours(parsedCsvTime.hours)
    killDate.setMinutes(parsedCsvTime.minutes)
    killDate.setSeconds(parsedCsvTime.seconds)
    killDate.setMilliseconds(parsedCsvTime.milis)

    const { time, ttk, ...rest } = r
    return {
      ...rest,
      ttk: parseFloatOrThrow(
        r.ttk.split("s")[0],
        `ttk ${ttk} from csvEntity, Challenge ${fileName.full}`
      ),
      killDate
    }
  })
}

const getKillFromFilePropertyDescriptions = (): Header<KillFromFile> => {
  const { time, ttk, ...rest } = stripColon(getMergedKillPropertyDescriptions())
  return {
    ...rest,
    ttk: "TTK (in sec)",
    killDate: "Kill Date"
  }
}

export { mapKillFromFile, getKillFromFilePropertyDescriptions }
