import { parseIntOrThrow } from "@fat-dash/domain/util/number"

export function parseKovaakTime2010(kTime: string, errorDescr: string) {
  const [hours, minutes, secondsAndMilis] = kTime.split(":")
  const [seconds, milis] = secondsAndMilis.split(".")
  return {
    hours: parseIntOrThrow(
      hours,
      `hours ${hours} from csvEntity, Challenge ${errorDescr}`
    ),
    minutes: parseIntOrThrow(
      minutes,
      `minutes ${minutes} from csvEntity, Challenge ${errorDescr}`
    ),
    seconds: parseIntOrThrow(
      seconds,
      `seconds ${seconds} from csvEntity, Challenge ${errorDescr}`
    ),
    milis: parseIntOrThrow(
      milis,
      `miliseconds ${milis} from csvEntity, Challenge ${errorDescr}`
    )
  }
}
