import { CreateChallengeResult } from "@fat-dash/domain/csv-etl/types"
import { saveChallengeFilesScheduled } from "./saveChallengeFilesScheduled"
import { readFile } from "@fat-dash/infrastructure/fs/read-file"

type WorkerContext = {
  onmessage(this: Worker, ev: { data: File[] }): void
  postMessage(message: CreateChallengeResult): void
}

const workerCtx: WorkerContext = self as any

workerCtx.onmessage = async e => {
  saveChallengeFilesScheduled(
    readFile,
    workerCtx.postMessage,
    PENDING_COUNT_MAX
  )(e.data)
}

const PENDING_COUNT_MAX = navigator.hardwareConcurrency
