import { FileReadResult } from "@fat-dash/domain/csv-etl/types"

/*
 * Guaranteed to return a FileReadResult and not throwing an error
 */
function readFile(file: File): Promise<FileReadResult> {
  return new Promise<FileReadResult>(resolve => {
    const reader = new FileReader()
    reader.onload = () =>
      typeof reader.result === "string"
        ? resolve({
            fileName: file.name,
            fileContent: reader.result,
            errorMessage: null
          })
        : resolve({
            fileName: file.name,
            errorMessage: `wrong file read type for file ${file.name}.
        Expected string, but did get ArrayBuffer or null.`,
            fileContent: null
          })

    reader.onerror = e =>
      resolve({
        fileName: file.name,
        errorMessage: `${file.name} read error occured.`,
        event: e,
        fileContent: null
      })

    reader.readAsText(file)
  })
}

export { readFile }
