import createDebug from "debug"
import Log from "./Log"
import { APP_NS, Loglevel } from "./LogConfig"

const colorOf = (logLevel: Loglevel) => {
  switch (logLevel) {
    case Loglevel.trace:
      return "grey"
    case Loglevel.debug:
      return "#f42dff"
    case Loglevel.info:
      return "lightblue"
    case Loglevel.warn:
      return "#fbbc05"
    case Loglevel.error:
      return "red"
    default:
      throw new Error("Unkown log level.")
  }
}

/**
 * BASE:<MODULE_NAMESPACE>:<LOGLEVEL>
 */
class LogImpl implements Log {
  private appAndModuleNs: string

  constructor(moduleName?: string) {
    this.appAndModuleNs = moduleName ? `${APP_NS}:${moduleName}` : `${APP_NS}`
  }

  private generateMessage(logLevel: Loglevel, formatter: any, ...args: any[]) {
    const debug = createDebug(`${this.appAndModuleNs}:${logLevel}`)
    ;(debug as any).color = colorOf(logLevel)
    debug(formatter, ...args)
  }

  trace(formatter: any, ...args: any[]) {
    this.generateMessage(Loglevel.trace, formatter, ...args)
  }

  debug(formatter: any, ...args: any[]) {
    this.generateMessage(Loglevel.debug, formatter, ...args)
  }

  info(formatter: any, ...args: any[]) {
    this.generateMessage(Loglevel.info, formatter, ...args)
  }

  warn(formatter: any, ...args: any[]) {
    this.generateMessage(Loglevel.warn, formatter, ...args)
  }

  error(formatter: any, ...args: any[]) {
    this.generateMessage(Loglevel.error, formatter, ...args)
  }
}

export default LogImpl
