
function parseFloatOrThrow(maybeNumber: string, details : string) {
    let v = Number.parseFloat(maybeNumber);
    if (Number.isNaN(v)) {
        throw new Error(`Could not convert ${maybeNumber} to number. Details: ${details}`);
    } 
    return v;
}

function parseIntOrThrow(maybeNumber: string, details?: string) {
    let v = Number.parseInt(maybeNumber);
    if (Number.isNaN(v)) {
        throw new Error(`Could not convert ${maybeNumber} to number. Details: ${details}`);
    } 
    return v;
}

export {parseFloatOrThrow, parseIntOrThrow};